.pen {
	transition: transform 150ms;
	cursor: pointer;
	position: fixed;
	background-color: rgba(#424242,.42);
	width: 62px;
	height: 62px;
	bottom: 24px;
	right: 24px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	@include hover {
		transform: scale(1.05); }
	&:active {
		opacity: .85; }
	@include sm {
		width: 36px;
		height: 36px; }

	&__image {
		display: flex;
		justify-content: center;
		align-items: center;
		.icon {
			font-size: 25px;
			fill: $white; }
		@include sm {
			.icon {
				font-size: 20px; } } } }
